body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.search-form {
  justify-content: center;
  margin-top: 15px;
  display: flex;
}

.search-form input[name="searchQuery"] {
  border: none;
  border-radius: 4px;
  outline: none;
  margin-right: 10px;
  padding: 5px;
  font-size: 16px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  font-size: 16px;
}

.gallery {
  grid-gap: 10px;
  grid-template-columns: repeat(4, 1fr);
  margin-top: 20px;
  display: grid;
}

.gallery .photo-card {
  background-color: #f1f1f1;
  border-radius: 4px;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  display: flex;
}

.gallery .photo-card img {
  max-width: 100%;
  height: auto;
  border-radius: 4px;
}

.gallery .photo-card .info {
  margin-top: 10px;
}

.gallery .photo-card .info .info-item {
  margin-bottom: 5px;
}

.gallery .photo-card .info .info-item b {
  font-weight: bold;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
